*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Bebas Neue", cursive;
}

body {
  overflow-x: hidden;
  min-height: 500vh;
  background-color: #090E0E;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

button, input {
  background: transparent;
  border: none;
  outline: none;
}

.center-f, .gallery .slider-container, .about {
  display: flex;
  align-items: center;
}

.center-g, .gallery .image-container, header .container .header-img {
  display: grid;
  place-items: center;
}

.position-flow {
  position: absolute;
  top: 0;
  left: 0;
}

.smoothPointer, .gallery .slider-container .left-arrow,
.gallery .slider-container .right-arrow {
  transition: 0.5s;
  cursor: pointer;
}

.container {
  padding: 0 15px;
  margin: 0 auto;
}

.title h2 {
  font-size: 2.125rem;
  margin-bottom: 0.5rem;
  letter-spacing: 2px;
}
@media (min-width: 75rem) {
  .title h2 {
    font-size: 2.75rem;
    margin-bottom: 0.625rem;
  }
}
.title p {
  font-family: "Bona Nova", serif;
  margin-inline: 1rem;
}

/* ------------------------- Start style Container ------------------------- */
.container {
  padding: 0 15px;
  margin: 0 auto;
}

/* Small */
@media (min-width: 48rem) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 62rem) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 75rem) {
  .container {
    width: 1170px;
  }
}
/* ------------------------- End style Container ------------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

* {
  scrollbar-color: var(--drb-Main-Color) transparent;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 0.4em;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--drb-Main-Color);
  border: 3px solid transparent;
  border-radius: 5px 0 0 5px;
}

.mobile-nav {
  padding: 1.5rem 1.875rem;
}
.mobile-nav .logo img {
  height: 2.25rem;
  filter: brightness(50);
}
.mobile-nav .bottom-nav {
  background-color: rgba(255, 255, 255, 0.0470588235);
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  border-radius: 0.5rem 0.5rem 0 0;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  z-index: 9;
}
.mobile-nav .bottom-nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.65rem 1.5rem;
}
.mobile-nav .bottom-nav ul li {
  width: 40px;
}
.mobile-nav .bottom-nav ul li a {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mobile-nav .bottom-nav ul li a img {
  width: 2rem;
  transform: translateY(10px);
  filter: saturate(0);
  transition: 0.5s;
  scale: 0.85;
}
.mobile-nav .bottom-nav ul li a p {
  margin-top: 3px;
  font-size: 0.7rem;
  font-weight: 500;
  color: #38796b;
  white-space: nowrap;
  scale: 0;
  line-height: 15px;
  font-family: "Changa", sans-serif;
  transition: 0.5s;
}
.mobile-nav .bottom-nav ul li.active img {
  filter: unset;
  transform: translateY(0px);
  scale: 1;
}
.mobile-nav .bottom-nav ul li.active p {
  scale: 1;
  display: inline-block;
}
.mobile-nav .bottom-nav ul .flow-bubbol {
  position: absolute;
  top: -20px;
  background: radial-gradient(#41c6c6, #235050);
  padding: 0.9rem 0.65rem;
  border-radius: 50%;
  box-shadow: 0 0 16px 16px rgba(83, 198, 198, 0.2117647059);
  animation: flowBubbol 4s infinite alternate;
  transition: 0.5s;
}

@keyframes flowBubbol {
  50% {
    box-shadow: 0 0 16px 5px rgba(83, 198, 198, 0.2117647059);
  }
  100% {
    box-shadow: 0 0 16px 16px rgba(83, 198, 198, 0.2117647059);
  }
}
.desktop-nav .container {
  display: flex;
  padding-block: 1.25rem;
}
.desktop-nav .container img {
  height: 2.813rem;
  margin-right: 6rem;
}
.desktop-nav .container ul {
  width: 50%;
  display: flex;
  align-items: center;
}
@media (min-width: 62rem) {
  .desktop-nav .container ul {
    margin-left: auto;
  }
}
.desktop-nav .container ul li {
  margin-inline: 1.25rem;
  transition: 0.5s;
}
@media (min-width: 62rem) {
  .desktop-nav .container ul li {
    margin-inline: 0.5rem;
  }
}
@media (min-width: 75rem) {
  .desktop-nav .container ul li {
    margin-inline: 1.5rem;
  }
}
.desktop-nav .container ul li a {
  color: #fff;
  font-size: 0.875rem;
  white-space: nowrap;
  font-family: "Changa", sans-serif;
  position: relative;
}
.desktop-nav .container ul li a::after {
  content: "";
  position: absolute;
  bottom: 5px;
  left: 0;
  height: 0.125rem;
  width: 0;
  background-color: #41c6c6;
  transition: 0.5s;
}
.desktop-nav .container ul li:hover:not(li:last-child) a::after {
  width: 100%;
}
.desktop-nav .container ul li:last-child {
  margin-left: 2.4rem;
}
.desktop-nav .container ul li:last-child a {
  color: #000;
  background-color: #fff;
  padding: 0.45rem 1.65rem;
  border-radius: 0.313rem;
  transition: 0.5s;
}
.desktop-nav .container ul li:last-child a:hover {
  background-color: transparent;
  color: #fff;
  outline: 1px solid #fff;
}
.desktop-nav .container ul li:first-child {
  margin-left: 1.5rem;
}

