.gallery {
    color: #fff;
    text-align: center;
    padding-blocK: 2rem;
    overflow: hidden;
    min-height: 70vh;
    @media (min-width: 48rem) {
        & {
            padding-blocK: 6rem;
        }
    }
    .slider-container {
        @extend .center-f;
        position: relative;
        width: 100%;
        min-height: 200px;
        margin-top: 3rem;
        justify-content: center;
        transition: .5s;
        @media (min-width: 48rem) {
            & {
                min-height: 300px;
            }
        }
        @media (min-width: 62rem) {
            & {
                min-height: 450px;
            }
        }
        .slider {
            position: absolute;
            display: flex;
            transition: 1s;
            .img {
                width: 150px;
                height: 100px;
                margin-inline: 0.75rem;
                opacity: .6;
                background-repeat: no-repeat;
                background-size: cover;
                transition: .5s;
                @media (min-width: 48rem) {
                    & {
                        width: 275px;
                        height: 200px;
                        margin-inline: 1.125rem;
                    }
                }
                @media (min-width: 62rem) {
                    & {
                        width: 360px;
                        height: 250px;
                    }
                }
            }
            .focus {
                scale: 1.35;
                margin-inline: 2.75rem;
                opacity: 1;
                cursor: pointer;
                &:active {
                    scale: 1.25;
                }
                @media (min-width: 48rem) {
                    & {
                        margin-inline: 4.375rem;
                    }
                }
                @media (min-width: 62rem) {
                    & {
                        margin-inline: 7.5rem;
                    }
                }
            }
        }
        .top-corner,
        .bottom-corner {
            height: 35px;
            width: 35px;
            border: 3px solid;
            position: absolute;
            left: 50%;
            @media (min-width: 48rem) {
                & {
                    height: 45px;
                    width: 45px;
                }
            }
            @media (min-width: 62rem) {
                & {
                    height: 60px;
                    width: 60px;
                }
            }
        }
        .top-corner {
            top: -15px;
            transform: translate(-130px);
            border-color: #1a9999 transparent transparent #1a9999;
            @media (min-width: 48rem) {
                & {
                    top: -35px;
                    transform: translate(-220px);
                }
            }
            @media (min-width: 62rem) {
                & {
                    top: -15px;
                    transform: translate(-295px);
                }
            }
        }
        .bottom-corner {
            top: 180px;
            transform: translate(95px);
            border-color: transparent #1a9999 #1a9999 transparent;
            @media (min-width: 48rem) {
                & {
                    top: 290px;
                    transform: translate(180px);
                }
            }
            @media (min-width: 62rem) {
                & {
                    top: 395px;
                    transform: translate(240px);
                }
            }
        }
        .left-arrow,
        .right-arrow {
            @extend .smoothPointer;
            height: 20px;
            width: 20px;
            border: 3px solid;
            position: absolute;
            opacity: .5;
            @media (min-width: 62rem) {
                & {
                    height: 25px;
                    width: 25px;
                    border: 4px solid;
                }
            }
        }
        .left-arrow:hover,
        .right-arrow:hover {
            opacity: 1;
        }
        .left-arrow {
            transform: translateX(20px) translateY(-5px) rotate(45deg);
            border-color: transparent transparent #ffffff #ffffff;
            left: 0;
        }
        .right-arrow {
            transform: translateX(-20px) translateY(-5px) rotate(45deg);
            border-color: #ffffff #ffffff transparent transparent;
            right: 0;
        }
    }
    .image-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #000000e8;
        @extend .center-g;
        z-index: 9;
        display: none;
        .close-btn {
            position: fixed;
            top: 1rem;
            right: 1rem;
            img {
                height: 2rem;
                cursor: pointer;
                filter: invert(1);
            }
        }
        .focused-img {
            width: 90%;
            height: 80%;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
            opacity: 0;
            transform: translateY(-2rem);
            transition: 1s;
        }
    }
}